import { Container, Button } from "./styled";

const Buttons = ({ tasks, hideDone, toggleHideDone, setAllDone }) => (
    <Container>
        {tasks.length > 0 && (
            <>
                <Button
                    onClick={toggleHideDone}
                    disabled={tasks.every(({ done }) => !done)}
                >
                    {hideDone ? "PokaÅ¼" : "Ukryj"} ukoÅ„czone
                </Button>
                <Button
                    onClick={setAllDone}
                    disabled={tasks.every(({ done }) => done)}>
                    UkoÅ„cz wszystkie
                </Button>
            </>
        )}
    </Container>
);

export default Buttons;